import { Component } from '@angular/core';
import {ModalController, NavController, NavParams, ViewController} from 'ionic-angular';
import {AuthServiceProvider} from "../../../providers/auth-service/auth-service";
import {CustomerServiceProvider} from "../../../providers/customer-service/customer-service";


@Component({
  selector: 'page-info',
  templateUrl: 'nolocation.html',
})
export class NolocationPage {

  customers:any;
  salesman:any;
  loading:boolean = false;

  constructor(public navCtrl: NavController , public auth: AuthServiceProvider , public noloc:CustomerServiceProvider, public modalCtrl: ModalController) {
    this.salesman = this.auth.supervisor? this.auth.selectedSalesman : this.auth.getUserInfo();
  }

  ionViewWillEnter(){
    this.setData();
  }

  setData(){
    this.loading = true;
    return this.noloc.get_noLocation(this.salesman)
      .subscribe(res=>{
        this.customers = res;
        this.loading = false;
      }, err => {this.loading = false; this.navCtrl.pop()});
  }

  itemSelected(item){
    let profileModal = this.modalCtrl.create(NoLocModal, { info: item });
    profileModal.present();
  }

}


/**********************************************************************************/
/**********************************************************************************/
/**
 *  Info Modal
 */
@Component({
  selector: 'page-info',
  templateUrl: 'infoModal.html'
})

export class NoLocModal {

  info:any;

  constructor(params: NavParams, public viewCtrl:ViewController) {
    this.info = params.get('info');
  }

  dismiss() {
    this.viewCtrl.dismiss();
  }

}



