import {Component} from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { CustomerServiceProvider} from "../../../providers/customer-service/customer-service";


@Component({
  selector: 'page-info',
  templateUrl: 'visited.html',
})
export class VisitedPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public waritex:CustomerServiceProvider) {
  }

  ionViewDidLoad() {

  }

  goNotVisitLocation(marker){
    this.navCtrl.pop();
    this.navCtrl.parent.select(0);
    marker.getMap().setCenter(marker.getPosition());
    marker.getMap().setZoom(17);
  }


}



