import { Component , ViewChild , ElementRef } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { LoadingController , Loading , AlertController } from 'ionic-angular';
import { Geolocation } from '@ionic-native/geolocation';
import { AuthServiceProvider } from "../../providers/auth-service/auth-service";
import {CustomerServiceProvider} from "../../providers/customer-service/customer-service";
import {Observable} from "rxjs";
import {startWith, switchMap} from "rxjs/operators";
import { Platform } from 'ionic-angular';
import { ActionSheetController } from 'ionic-angular'

declare var google:any;
const AutoRefresh = 40000;

@Component({
  selector: 'page-waritex',
  templateUrl: 'waritex.html',
})
export class Waritex {
  map: any;
  activeInfoWindow:any;
  activeInfoWindowMarker:any;
  bounds:any;
  markers:any=[];
  customers:any;
  visited:any=[];
  notVisited:any=[];
  loading:Loading;
  polling:any;
  firstShow:number = 0;
  salesman:any;
  selectedSalesman:any;

  Latitude:any;
  Longitude:any;
  currentLocation:any;

  @ViewChild('maps') mapRef: ElementRef;

  constructor(public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController , private geolocation: Geolocation , public auth:AuthServiceProvider , private waritex:CustomerServiceProvider , public alertc:AlertController,public platform: Platform,public actionSheetCtrl: ActionSheetController) {
    this.salesman = this.auth.getUserInfo();
    this.stopPollingInBackground();
  }

  ionViewDidLoad() {
    try{
      this.showMap();
      this.pollingDate();
    }
    catch (e) {
      // this.presentLoading();
      this.showError('Try Load...');
    }
  }

  ngOnDestroy() {
    this.polling.unsubscribe();
    this.polling = new Observable();
    this.loading.dismiss();
  }

  ionViewWillLeave(){
    // this.polling.unsubscribe();
    // this.loading.dismissAll();
  }

  stopPollingInBackground(){
    this.platform.ready().then(() => {
      if (this.platform.is('cordova')){
        //Subscribe on pause
        this.platform.pause.subscribe(() => {
          this.polling.unsubscribe();
          this.loading.dismiss();
        });
        //Subscribe on resume
        this.platform.resume.subscribe(() => {
          this.salesman = this.auth.getUserInfo();
          this.pollingDate();
        });
      }
    });
  }

  presentLoading() {
    const loader = this.loadingCtrl.create({
      content: "Please wait...",
      duration: 1000
    });
    loader.present();
    var th = this;
    loader.onDidDismiss(function () {
      th.ionViewDidLoad()
    })
  }

  // attach info label to marker (customer)
  attachInfo(marker, customer) {
    var infowindow = new google.maps.InfoWindow({
      content: '' +
        '<h4>اسم الزبون: ' + customer.CustomerName + '</h4>' +
        '<div>مكانه: &nbsp;<a href="https://www.google.com/maps/search/?api=1&query=' + customer.Lat + ',' + customer.Lng + '" target="_blank">الذهاب إليه</a></div>'
    });
    let flag = this;
    infowindow.addListener('closeclick' , function () {
      flag.activeInfoWindowMarker = null;
      flag.activeInfoWindow = null;
    });
    marker.addListener('click', function () {
      if (flag.activeInfoWindow) {
        flag.activeInfoWindow.close();
        flag.activeInfoWindowMarker = null;
        flag.activeInfoWindow = null;
      }
      infowindow.open(marker.get('map'), marker);
      flag.activeInfoWindow = infowindow;
      flag.activeInfoWindowMarker = marker
    });
  }

  // ReOpen InfoWindow
  reOpenInfoWindow(){
    if (this.activeInfoWindow)
      this.activeInfoWindow.open(this.map,this.activeInfoWindowMarker)
  }

  showCustomers() {
    let customers = this.customers;
    this.visited=[];
    this.notVisited=[];
    this.markers = [];
    // define icon images
    let doneicon = 'assets/imgs/visitDone.png';
    let yeticon = 'assets/imgs/visitYet.png';
    // init map's bounds
    this.bounds = new google.maps.LatLngBounds();
    let j=0;
    for (let i = 0; i < customers.length; i++) {
      let positiono = new google.maps.LatLng(Number(customers[i].Lat), Number(customers[i].Lng));
      let marker = new google.maps.Marker({
        map: this.map,
        position: positiono,
        title: customers[i].CustomerName,
        icon: customers[i].visited === 1 ? doneicon : yeticon,
        zIndex: customers[i].visited === 1 ? 1 : 2,
      });

      if(this.auth.isSupervisor() && customers[i].visited === 1){
        j++;
        let label = {
          text: (String)(j),
          color: 'yellow'
        };
        marker.setLabel(label);
      }

      this.bounds.extend(positiono);
      this.attachInfo(marker, customers[i]);
      this.markers.push(marker);
      if (customers[i].visited === 1){
        this.visited.push({info:customers[i],marker:marker})
      }
      else{
        this.notVisited.push({info:customers[i],marker:marker})
      }
    }
    this.waritex.setCustomers(this.customers);
    this.waritex.setVisited(this.visited);
    this.waritex.setNotVisited(this.notVisited);
  }

  // createLayer(customers){
  //   for (var i = 0; i < customers.length; i++) {
  //     var positiono = new google.maps.LatLng(Number(customers[i].Lat), Number(customers[i].Lng))
  //     let l1 = new google.maps.MVCObject();
  //     l1.set({visited: null});
  //     let parkMarker = new google.maps.Marker({position: positiono,});
  //     this.attachInfo(parkMarker, 'asd');
  //     parkMarker.bindTo('map', l1, 'visited');
  //     l1.set('visited', this.map);
  //   }
  // }

  showError(text , status=0) {
    this.loading.dismiss();

    let alert = this.alertc.create({
      title: 'Error',
      subTitle: text,
      buttons: ['OK']
    });
    alert.present();
  }

  showLoading() {
    this.loading = this.loadingCtrl.create({
      content: 'Please wait...',
      dismissOnPageChange: true
    });
    this.loading.present();
  }

  myLocation(){
    try {
      this.geolocation.getCurrentPosition().then((resp) => {
        // resp.coords.latitude
        // resp.coords.longitude
        alert(resp.coords.latitude);
        var locmypos = new google.maps.LatLng(resp.coords.latitude, resp.coords.longitude);
        new google.maps.Marker({
          map: this.map,
          position: locmypos,
          title: 'My Location',
          label: 'My Location',
        });
        this.bounds.extend(locmypos);
        this.map.fitBounds(this.bounds);       // auto-zoom
        this.map.panToBounds(this.bounds);     // auto-center
      }).catch((error) => {
        console.log('Error getting location', error.message);
      });
    }
    catch (e) {
      this.showError(e);
    }
  }

  pollingDate(){
    if (this.auth.supervisor && !this.selectedSalesman)
      return;
    let salesman = this.auth.supervisor? this.selectedSalesman : this.salesman;
    this.polling = Observable.interval(AutoRefresh)
      .pipe(
        startWith(0),
        switchMap(() => {
          this.showLoading();
          return this.waritex.get_customers(salesman);
        })
      )
      .subscribe(
        res => {
          this.customers = res;
          this.loading.dismiss();
          console.log('done auto refresh');
          this.clearMarkers();
          this.showCustomers();
          this.reOpenInfoWindow();
          if (this.firstShow === 0) {
            this.firstShow++;
            this.autoViewAll();
          }
        }, er => this.loading.dismiss()
        , () => this.loading.dismissAll()
      );
  }

  supervisor(){
    let buttons = this.makeButtons();
    const actionSheet = this.actionSheetCtrl.create({
      title: 'اختر الموزع',
      buttons: buttons
    });
    actionSheet.present();
  }

  makeButtons(){
    let buttons = [];
    let salesmans = this.auth.salesmans;
    for(let i=0; i<salesmans.length; i++){
      buttons.push({
        text: salesmans[i].name,
        role: 'destructive',
        handler: () => {
          this.selectedSalesman = salesmans[i].code;
          this.auth.selectedSalesman = this.selectedSalesman;
          if (this.polling){
            this.polling.unsubscribe();
            this.polling = new Observable();
            try {
              this.loading.dismiss();
              this.activeInfoWindow.close();
              this.activeInfoWindowMarker = null;
              this.activeInfoWindow = null;
            }catch (e) {}
          }
          this.firstShow = 0;
          this.pollingDate();
        }
      });
    }
    return buttons;
  }


  // Maps Functions
  /******************************************************************/
  /******************************************************************/
  // Init Google Map
  showMap(){
    const location1 = new google.maps.LatLng(33.248952,44.390661);
    const mapOptions = {
      zoom: 17,
      center: location1,
      fullscreenControl: false,
      mapTypeControl: false,
      streetViewControl: false,
    };

    this.map = new google.maps.Map(this.mapRef.nativeElement , mapOptions)
  }

  // Auto Zoom & Focus on Markers
  autoViewAll(){
    this.bounds = new google.maps.LatLngBounds();
    for (var i = 0; i < this.markers.length; i++) {
      var loc = new google.maps.LatLng(this.markers[i].position.lat(), this.markers[i].position.lng());
      this.bounds.extend(loc);
    }
    this.map.fitBounds(this.bounds);       // auto-zoom
    this.map.panToBounds(this.bounds);     // auto-center
  }

  // Sets the map on all markers in the array.
  setMapOnAll(map) {
    for (var i = 0; i < this.markers.length; i++) {
      this.markers[i].setMap(map);
    }
  }

  // Removes the markers from the map, but keeps them in the array.
  clearMarkers() {
    this.setMapOnAll(null);
  }

  // Shows any markers currently in the array.
  showMarkers() {
    this.setMapOnAll(this.map);
  }

  // Deletes all markers in the array by removing references to them.
  deleteMarkers() {
    this.clearMarkers();
    this.markers = [];
  }
  /******************************************************************/
  /******************************************************************/


// Get geo coordinates

  getMapLocation() {

    this.geolocation.getCurrentPosition
    ({ enableHighAccuracy: true }).then(pos=>this.onMapSuccess(pos)).catch(err=>this.onMapError(err));
  }

// Success callback for get geo coordinates

  onMapSuccess(position) {
    this.Latitude = position.coords.latitude;
    this.Longitude = position.coords.longitude;
    this.getMap(this.Latitude, this.Longitude);

  }

// Get map by using coordinates

  getMap(latitude, longitude) {
    var latLong = new google.maps.LatLng(latitude, longitude);
    if (!this.currentLocation===undefined){
      this.currentLocation.setMap(null);
    }
    this.currentLocation = new google.maps.Marker({
      position: latLong
    });
    this.currentLocation.setMap(this.map);
    this.map.setZoom(15);
    this.map.setCenter(this.currentLocation.getPosition());
  }

// Success callback for watching your changing position

  onMapWatchSuccess(position) {

    var updatedLatitude = position.coords.latitude;
    var updatedLongitude = position.coords.longitude;

    if (updatedLatitude != this.Latitude && updatedLongitude != this.Longitude) {

      this.Latitude = updatedLatitude;
      this.Longitude = updatedLongitude;

      this.getMap(updatedLatitude, updatedLongitude);
    }
  }

// Error callback

  onMapError(error) {
    console.log('code: ' + error.code + '\n' +
      'message: ' + error.message + '\n');
  }

// Watch your changing position

  watchMapPosition() {
    return navigator.geolocation.watchPosition
    (this.onMapWatchSuccess, this.onMapError, { enableHighAccuracy: true });
  }



}



