import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from "rxjs";

class User {
  code:string;
  username:string;
  name:string;
  supervisor:boolean;

  constructor(username , code , name , supervisor=false){
    this.code = code;
    this.username = username;
    this.name = name;
    this.supervisor = supervisor;
  }
}

@Injectable()
export class AuthServiceProvider {

  APIURL:string = 'http://waritex.com';
  //APIURL:string = 'http://localhost/waritexGoogle/public';

  user:User;
  currentUser:string;
  supervisor:boolean = false;
  salesmans:any;
  selectedSalesman:any;

  constructor(public http: HttpClient) {

  }

  public login(username, password){
    if (username===null || username===undefined || username===""){
      return Observable.throw("Please insert credentials");
    }
    else {

      return Observable.create(observer => {
        // At this point make a request to your backend to make a real check!
        this.http.post(this.APIURL+'/login' , {username: username})
          .subscribe((user: User) => {
            this.currentUser = user.code;
            this.user = user;
            if(user.supervisor){
              this.supervisor = true;
              this.http.post(this.APIURL+'/get_all_salesman' , {code: user.code})
                .subscribe(
                  (salesmans) => {
                    this.salesmans = salesmans;
                    let access = ( user );
                    observer.next(access);
                    observer.complete();
                  }
                , er => observer.error(er.error.message)
                )
            }
            else{
              let access = ( user );
              observer.next(access);
              observer.complete();
            }
          } , er => observer.error(er.error.message) );
      });
    }
  }

  public getUserInfo() {
    return this.currentUser;
  }

  public isSupervisor(){
    return this.supervisor;
  }

  public logout() {
    return Observable.create(observer => {
      this.currentUser = null;
      observer.next(true);
      observer.complete();
    });
  }

}



